.main-visual {
  height: 35vw;
  max-height: 300px;
  background-size: cover;
  @include vender(display, flex);
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @include query(tablet) {
    background: unquote("rgba(#{$palette-grey-100}, 1)");
  }
}



